$theme-color: #30A64A;

html {
  min-height: 100%;
  position: relative;
}

.page-content-wrap {
  @media screen and (max-width: 600px) {
    width: 100%;
  }
  margin: 0 auto;
}

body {
  @media screen and (min-width: 800px) {
    background-image: url("../images/allStagesSpriteVertical.png");
  }
  font-family: 'Open Sans', sans-serif;
  margin-bottom: 60px;
  background-repeat: repeat-y;
  background-position: left 60px;
}

a {
  color: inherit;
}

a:hover {
  color: $theme-color;
}

h1 {
  color: $theme-color;
}

hr {
  width: 100%;
}

.navbar-light .navbar-nav .nav-link {
  color: black;
  font-size: 1.1rem;
}

.jumbotron {
  background-image: url("../images/zencode_logo.png"), url("../images/zencode-banner-blurred.png");
  @media screen and (max-width: 600px) {
    background-size: 85%, cover;
    min-height: 10em;
  }
  background-size: 600px, cover;
  background-repeat: no-repeat;
  background-position: center;
  min-height: 20em;
  margin-bottom: 0;
}

.footer {
  // background-color: #f5f5f5;
  border-top: 1px solid rgba(0,0,0,.1);
  bottom: 0;
  height: 35px;
  width: 100%;
  position: absolute;
}

.img_div img {
  padding: 0.5em;
}

.card-body {
  @media screen and (max-width: 600px) {
    padding: 0.5rem;
  }
}

.profiles .card {
    width: 17.5em;
  }

.profiles .card-img-top {
  // max-height: 15em;
  width: 100%;
  height: auto;
}

.org-img {
  height: auto;
  width: auto;
  max-width: 70%;
  max-height: 5em;
}

.org-img-lg {
  height: auto;
  width: auto;
  max-height: 5em;
  align-self: center;
  margin-top: 0.5rem;
}

.profile-img {
  height: auto;
  width: auto;
  max-width: 15em;
  align-self: center;
}

.profile {
  max-width: 60em;
  margin-left: 120px;
}

.profile-text {
  columns: 2 20em;
}

.no-column-break {
  display: inline-block;
}

.profile h2,
.profile h3,
.profile h4 {
  color: $theme-color;
}

.secondment-entry img {
  height: auto;
  width: auto;
  max-width: 100%;
}

.secondments .card {
  width: 48%;
  max-width: 28em;
  flex: 1 0 auto;
}

.text-container {
  margin-top: 1em;
  padding: 1em;
  max-width: 60em;
  background-color: rgba(250,250,250,0.75);
  @media screen and (min-width: 800px) {
    margin-left: 120px;
  }

  a {
    text-decoration: underline;
  }
}

.post-image {
  display: block;
  height: auto;
  width: auto;
  max-width: 100%;
  max-height: 30em;
}

.post-image-lg {
  @extend .post-image;
  max-height: 50em;
}

.post-image-sm {
  height: auto;
  width: 50%;
}

.item .post-image:hover {
  cursor: zoom-in;
}

.post-image-jenga {
  display: inline-block;
  width: 100%;
  height: auto;
}

.image-gallery {
  max-height: 1550px;
}

.image-gallery-lg {
  height: 100%;
  @media screen and (max-width: 600px) {
    width: 100%;
    height: auto;
  }

  .item {
    padding: 5px;
  }
}

.image-gallery-jenga {
  margin-top: 2em;
  column-count: 3;
  column-gap: 0;
  width: 85vw;
  // height: 100vh;
}

.vertical-bar {
  border-left: 1.2pt solid grey;
  margin-right: 0.25em;
}

.social-media-div {
  color: $theme-color;
  width: 50%;
  a {
    text-decoration: none;
  &:hover{
    text-decoration:underline;  
  }
  }
}

#map {
  position: relative;
  width: 100%;
  max-width: 960px;
  margin: auto;
}

.maphover {
  padding: 0;
  border: 0;
  background-color: transparent;
  box-shadow: 0;
  display: flex;
  flex-direction: column;

  & img {
    margin-bottom: 0.5em;
  }
}

.background-sprite {
  margin-left: auto;
  width: 120px;
  height: 520px;
  background: url("../images/allStagesSprite.png") left center;
  animation: play 4s steps(36) infinite;
  margin: 0 auto;
}
@keyframes play {
  100% {
    background-position: -4320px;
  }
}

.video-container {
  width: 100%;
}

.video-container-square {
  width: 500px;
  height: 500px;
  object-fit: fill;
}

.twitter {
  padding-top: 1em;
  width: 80%;
  margin: auto;
}

.partner {
  width: 100%;
  // max-width: 50em;
  & .twitter-tag {
    float: right;
  }
}
